<template>
  <detail-component :ajax="ajax" />
</template>

<script>
import service from '@/service';
import DetailComponent from '@/components/NewsNoticeDetail';
export default {
  name: 'NewsDetail',
  data() {
    return {
      ajax: {
        // 
        detail: {
          method: 'post',
          urls: service.news.getDetail(),
          flag: this.$route.params.flag, // 01
          args: {
            id: this.$route.params.id // id
          }
        },
        // 
        comments: {
          // 
          get: {
            method: 'post',
            url: service.news.getComments(),
            args: {
              id: this.$route.params.id, // id
              offset: 0,
              limit: 8,
              type: this.$route.params.type // 
            }
          },
          // 
          set: {
            method: 'post',
            url: service.news.sendMsg(),
            args: {
              news_id: this.$route.params.id,
              content: '',
              type: this.$route.params.type
            }
          }
        },
        // 
        reply: {
          // 
          get: {
            method: 'post',
            url: service.news.getReply(),
            args: {
              // comment_idkey
              // id: [String, Number], // id
              // offset: [Number],
              // limit: [Number]
            }
          },
          // 
          set: {
            method: 'post',
            url: service.news.replyComment(),
            placeholder: '',
            args: {
              news_id: this.$route.params.id,
              comment_id: 0,
              content: '',
              type: this.$route.params.type // 
              // to_memid: 0, // 
              // to_adminid: 0 // 
            }
          }
        }
      }
    };
  },
  components: {
    DetailComponent
  }
};
</script>

<style lang="stylus" scoped>
</style>




